html,
body {
  width: 100%;
  height: 100%;
}
body {
  padding-bottom: 0 !important;
  font-family: 'Gorgia';
  font-size: 16px;
  color: #606060;
}
* {
  fill: #606060;
}
.Page {
  position: absolute;
  top: 64px;
  width: 100%;
  left: 0px;
}
.Page-name {
  color: #006bb9;
}
.logo {
  color: #0000ff;
  font-size: 1.5em;
}
.icon-space {
  position: relative;
  top: 4px;
  left: 5px;
}
.inline-list li {
  display: inline;
}
.inline-list.menu-list li {
  position: relative;
  padding: 0px 1em;
}
.inline-list.menu-list li::before {
  content: "/";
  position: absolute;
  right: 0px;
  top: 0px;
}
.inline-list.menu-list li:last-child::before {
  content: "";
}
.inline-list.menu-list li.active a {
  color: #006bb9;
}
.inline-list.menu-list li a {
  position: relative;
}
.inline-list.menu-list li a::after {
  content: '';
  position: absolute;
  height: 1px;
  width: 0;
  left: 50%;
  bottom: -5px;
  border-bottom: 1px solid #006bb9;
  transition: all 0.2s ease;
}
.inline-list.menu-list li a:hover::after {
  width: 100%;
  left: 0;
}
