.info-container {
  width: 340px;
  background-color: #fff;
  margin-left: 2em;
  margin-top: 40px;
  padding: 2em;
  line-height: 1em;
  position: absolute;
  color: #afafaf;
}
.info-container .header {
  padding: 0.5em;
  border-bottom: 1px solid #606060;
}
.info-container .header .title {
  font-weight: normal;
  color: #006bb9;
  padding: 0px;
  margin: 0px;
  line-height: 0.8em;
  font-size: 2em;
}
.info-container .header .subtitle {
  color: #006bb9;
  line-height: 1em;
}
.info-container .header .info-icon {
  cursor: pointer;
}
.info-container .header .info-icon * {
  fill: #d7dddd;
}
.info-container .header .info-icon:hover * {
  fill: #006bb9;
}
.info-container .close-it {
  position: absolute;
  top: 10px;
  right: 10px;
  transition: all .25s ease;
  cursor: pointer;
}
.info-container .close-it * {
  fill: #d7dddd;
}
.info-container .close-it:hover * {
  fill: #afafaf;
}
.info-container .module {
  margin: 1.5em 0;
}
.info-container .module.rank .chart {
  color: #333333;
}
.info-container .module.rank .chart .title {
  margin-bottom: 0.5em;
}
.info-container .module.rank .chart .data .value {
  border: 2px solid #00ccaa;
  border-radius: 50%;
  height: 80px;
  width: 80px;
}
.info-container .module.rank .chart .tendency path {
  fill: #d7dddd;
}
.info-container .module.rank .chart .tendency.active path {
  fill: #00ccaa;
}
.info-container .module.rank .mini-list {
  font-size: 0.9em;
}
.info-container .module.rank .mini-list .title {
  margin-bottom: 0.5em;
}
.info-container .module.rank .mini-list .list ul {
  padding: 0px;
  margin: 0px;
}
.info-container .module.rank .mini-list .list ul li {
  list-style-type: none;
}
.info-container .module.rank .mini-list .list ul li.active {
  color: #00ccaa;
}
.info-container .module.rank .mini-list .list ul li .number {
  margin-right: 0.5em;
}
.info-container .module.median {
  position: relative;
}
.info-container .module.median .title {
  margin: 0.5em;
  text-align: center;
}
.info-container .module.median .gradient {
  /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#ffffff+0,22ea9d+53,000000+100 */
  background: #ffffff;
  /* Old browsers */
  background: -moz-linear-gradient(left, #ffffff 0%, #22ea9d 53%, #000000 100%);
  /* FF3.6+ */
  background: -webkit-gradient(linear, left top, right top, color-stop(0%, #ffffff), color-stop(53%, #22ea9d), color-stop(100%, #000000));
  /* Chrome,Safari4+ */
  background: -webkit-linear-gradient(left, #ffffff 0%, #22ea9d 53%, #000000 100%);
  /* Chrome10+,Safari5.1+ */
  background: -o-linear-gradient(left, #ffffff 0%, #22ea9d 53%, #000000 100%);
  /* Opera 11.10+ */
  background: -ms-linear-gradient(left, #ffffff 0%, #22ea9d 53%, #000000 100%);
  /* IE10+ */
  background: linear-gradient(to right, #ffffff 0%, #22ea9d 53%, #000000 100%);
  /* W3C */
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#ffffff', endColorstr='#000000', GradientType=1);
  /* IE6-9 */
  opacity: .6;
  border-radius: 1.75em;
  position: relative;
  width: 100%;
  height: 2.5em;
}
.info-container .module.median .gradient::after {
  content: '';
  position: absolute;
  width: 1px;
  height: 100%;
  left: 50%;
  border-left: 1px dashed #606060;
}
.info-container .module.median .gradient .value {
  height: 2.5em;
  width: 2.5em;
  border-radius: 50%;
  position: absolute;
  top: 0px;
  color: #fff;
}
.info-container .module.median .gradient .value.max {
  border: 1px solid;
  right: 0px;
  color: #fff;
}
.info-container .module.median .gradient .value.min {
  color: #606060;
}
.info-container .module.median .gradient .value.actual {
  color: #000;
  transition: all 0.5s ease-out;
  background-color: #00ccaa;
  left: 0%;
  margin-left: -1.25em;
  z-index: 1;
}
.info-container .module:last-child {
  margin-bottom: 0px;
}
.info-container .country_list {
  position: absolute;
  left: 100%;
  top: 0px;
  height: 100%;
  background-color: #fff;
  overflow-y: auto;
  width: 300px;
}
.info-container .country_list md-content {
  height: 100%;
}
